// components/PostPreview.tsx
import React from 'react';
import { 
  View, 
  Text, 
  Image, 
  StyleSheet, 
  TouchableOpacity, 
  ImageSourcePropType,
  ImageProps as RNImageProps
} from 'react-native';

// Î™ÖÏãúÏ†ÅÏù∏ Ïù¥ÎØ∏ÏßÄ ÌîÑÎ°≠Ïä§ ÌÉÄÏûÖ Ï†ïÏùò
interface CustomImageProps extends Partial<RNImageProps> {
  source: ImageSourcePropType;
}
// ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ Ïú†Ìã∏ Ìï®Ïàò (UTC to KST Î≥ÄÌôò)
const formatDate = (dateString: string): string => {
  try {
    // Ï†ÑÎã¨Î∞õÏùÄ ÎÇ†Ïßú Î¨∏ÏûêÏó¥ÏùÑ Date Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
    const date = new Date(dateString);
    
    // Î™ÖÏãúÏ†ÅÏúºÎ°ú Ï¥àÍ∏∞ ÏãúÍ∞ÑÏùÑ 12:00Î°ú ÏÑ§Ï†ï
    const koreanTime = new Date(Date.UTC(
      date.getUTCFullYear(), 
      date.getUTCMonth(), 
      date.getUTCDate(), 
      12, // 12ÏãúÎ°ú Í≥†Ï†ï
      0   // Î∂ÑÏùÄ 0ÏúºÎ°ú Í≥†Ï†ï
    ));
    
    const year = koreanTime.getFullYear();
    const month = String(koreanTime.getMonth() + 1).padStart(2, '0');
    const day = String(koreanTime.getDate()).padStart(2, '0');
    const hours = String(koreanTime.getHours()).padStart(2, '0');
    const minutes = String(koreanTime.getMinutes()).padStart(2, '0');
    
    return `${year}-${month}-${day} ${hours}:${minutes}`;
  } catch (error) {
    // ÎÇ†Ïßú ÌååÏã± Ïã§Ìå® Ïãú ÏõêÎ≥∏ Î¨∏ÏûêÏó¥ Î∞òÌôò
    return dateString;
  }
};

// Í∞êÏ†ï ÌÉÄÏûÖ Ï†ïÏùò
interface Emotion {
  emotion_id: number;
  name: string;
  icon: string;
  color: string;
}

// ÌÉúÍ∑∏ ÌÉÄÏûÖ Ï†ïÏùò
interface Tag {
  tag_id: number;
  name: string;
}

// ÏÇ¨Ïö©Ïûê ÌÉÄÏûÖ Ï†ïÏùò
interface User {
  nickname: string;
  profile_image_url?: string;
}

// Ìè¨Ïä§Ìä∏ ÌÉÄÏûÖ Ï†ïÏùò
interface Post {
  post_id: number;
  title?: string;
  content: string;
  created_at: string;
  like_count: number;
  comment_count: number;
  is_anonymous: boolean;
  image_url?: string;
  emotions?: Emotion[];
  tags?: Tag[];
  user?: User | null;
}

// Ìè¨Ïä§Ìä∏ ÌîÑÎ¶¨Î∑∞ props ÌÉÄÏûÖ Ï†ïÏùò
interface PostPreviewProps {
  postType: 'myDay' | 'someoneDay' | 'comfort';
  post: Post;
  onPress: (postId: number) => void;
}

const PostPreview: React.FC<PostPreviewProps> = ({ postType, post, onPress }) => {
  // ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ
  const formattedDate = formatDate(post.created_at);
  
  // ÎÇ¥Ïö© ÏöîÏïΩ (ÏµúÎåÄ 100Ïûê)
  const summarizedContent = post.content.length > 100
    ? `${post.content.substring(0, 100)}...`
    : post.content;
  
  // Ìè¨Ïä§Ìä∏ ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨
  const handlePress = () => {
    onPress(post.post_id);
  };
  const renderImage = (source: ImageSourcePropType, style: object, testID?: string) => {
    const imageProps: CustomImageProps = {
      source,
      style,
      ...(testID ? { testID } : {})
    };

    return <Image {...imageProps} />;
  };
  
  return (
    <TouchableOpacity style={styles.container} onPress={handlePress}>
      <View style={styles.header}>
        <View style={styles.userInfo}>
          {!post.is_anonymous && post.user ? (
            <>
              {renderImage(
                post.user.profile_image_url 
                  ? { uri: post.user.profile_image_url } 
                  : require('../assets/images/default_avatar.png'),
                styles.avatar,
                'user-profile-image'
              )}
              <Text style={styles.username}>{post.user.nickname}</Text>
            </>
          ) : (
            <>
              {renderImage(
                require('../assets/images/anonymous_avatar.png'),
                styles.avatar,
                'anonymous-profile-image'
              )}
              <Text style={styles.username}>ÏùµÎ™Ö</Text>
            </>
          )}
        </View>
        
        <Text style={styles.date}>{formattedDate}</Text>
      </View>
      {/* Ìè¨Ïä§Ìä∏ ÎÇ¥Ïö© */}
      <View style={styles.content}>
        {/* Ï†úÎ™© (ÎàÑÍµ∞Í∞ÄÏùò ÌïòÎ£®, ÏúÑÎ°úÏùò Î≤ΩÏù∏ Í≤ΩÏö∞ÏóêÎßå) */}
        {(postType === 'someoneDay' || postType === 'comfort') && post.title && (
          <Text style={styles.title} numberOfLines={1} ellipsizeMode="tail">
            {post.title}
          </Text>
        )}
        
        {/* ÎÇ¥Ïö© ÏöîÏïΩ */}
        <Text style={styles.contentText} numberOfLines={3} ellipsizeMode="tail">
          {summarizedContent}
        </Text>
        
        {/* Í∞êÏ†ï ÌÉúÍ∑∏ (ÎÇ¥ ÌïòÎ£®Ïù∏ Í≤ΩÏö∞) */}
        {postType === 'myDay' && post.emotions && post.emotions.length > 0 && (
          <View style={styles.tagsContainer}>
            {post.emotions.map((emotion) => (
              <View 
                key={emotion.emotion_id} 
                style={[styles.tag, { backgroundColor: emotion.color + '30' }]}
              >
                <Text style={[styles.tagText, { color: emotion.color }]}>
                  {emotion.name}
                </Text>
              </View>
            ))}
          </View>
        )}
        
        {/* ÏùºÎ∞ò ÌÉúÍ∑∏ (ÎàÑÍµ∞Í∞ÄÏùò ÌïòÎ£®, ÏúÑÎ°úÏùò Î≤ΩÏù∏ Í≤ΩÏö∞) */}
        {(postType === 'someoneDay' || postType === 'comfort') && post.tags && post.tags.length > 0 && (
          <View style={styles.tagsContainer}>
            {post.tags.map((tag) => (
              <View key={tag.tag_id} style={styles.tag}>
                <Text style={styles.tagText}>#{tag.name}</Text>
              </View>
            ))}
          </View>
        )}
      </View>
      
          {/* Ïù¥ÎØ∏ÏßÄ (ÏûàÎäî Í≤ΩÏö∞) */}
      {post.image_url && (
        <View style={styles.imageContainer}>
          {renderImage(
            { uri: post.image_url },
            styles.image,
            'post-image'
          )}
        </View>
      )}
      {/* ÌïòÎã® (Ï¢ãÏïÑÏöî, ÎåìÍ∏Ä Ïàò) */}
      <View style={styles.footer}>
        <View style={styles.stat}>
          <Text style={styles.statIcon}>‚ô•</Text>
          <Text style={styles.statText}>{post.like_count}</Text>
        </View>
        <View style={styles.stat}>
          <Text style={styles.statIcon}>üí¨</Text>
          <Text style={styles.statText}>{post.comment_count}</Text>
        </View>
      </View>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#FFFFFF',
    borderRadius: 12,
    padding: 16,
    marginBottom: 16,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 3,
  },
  header: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginBottom: 12,
  },
  userInfo: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  avatar: {
    width: 24,
    height: 24,
    borderRadius: 12,
    marginRight: 8,
  },
  username: {
    fontSize: 14,
    fontWeight: '500',
    color: '#14171A',
  },
  date: {
    fontSize: 12,
    color: '#657786',
  },
  content: {
    marginBottom: 12,
  },
  title: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#14171A',
    marginBottom: 4,
  },
  contentText: {
    fontSize: 14,
    color: '#14171A',
    lineHeight: 20,
    marginBottom: 8,
  },
  tagsContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
  },
  tag: {
    backgroundColor: '#E8EDF0',
    borderRadius: 16,
    paddingHorizontal: 8,
    paddingVertical: 4,
    marginRight: 8,
    marginBottom: 4,
  },
  tagText: {
    fontSize: 12,
    color: '#4A6572',
  },
  imageContainer: {
    marginBottom: 12,
    borderRadius: 8,
    overflow: 'hidden',
  },
  image: {
    width: '100%',
    height: 150,
    resizeMode: 'cover',
  },
  footer: {
    flexDirection: 'row',
    borderTopWidth: StyleSheet.hairlineWidth,
    borderTopColor: '#E1E8ED',
    paddingTop: 12,
  },
  stat: {
    flexDirection: 'row',
    alignItems: 'center',
    marginRight: 16,
  },
  statIcon: {
    fontSize: 14,
    marginRight: 4,
    color: '#657786',
  },
  statText: {
    fontSize: 14,
    color: '#657786',
  },
});

export default PostPreview;